//App.js  
import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { GameProvider } from './stateManager/GameContext';
import Game from './components/Game';
import MonsterGame from './monster/MonsterGame';
import { OldApp } from './App';

function App() {
  return (

    <Router basename="/xplor">
      <Routes>

        <Route path="/" element={<NewGame />} />
        <Route path="" element={<NewGame />} />

        <Route path="/old" element={<OldApp />} />

        <Route path="/monster" element={<MonsterGame />} />
      </Routes>
    </Router>

  );
}

const NewGame = () => {
  <GameProvider>
    <Game />
  </GameProvider>;
}

export default App;
