// Terrain.js
// Contains functions related to terrain generation and chunk creation.

import * as PIXI from 'pixi.js';
import { createTerrainDetail } from './TerrainDetails';
import { generateNoiseValues, determineTerrainType, TERRAIN_INFO } from './TerrainTypes';
import { createTexturedTile } from './TerrainTextures';
import { config } from './config';
import { addTerrainFeatures } from './TerrainFeatures';


function generateTile(chunkX, chunkY, x, y, simplex, terrainTypeCache) {
  const worldX = chunkX * config.CHUNK_SIZE + x;
  const worldY = chunkY * config.CHUNK_SIZE + y;

  const noiseValues = generateNoiseValues(worldX, worldY, simplex);
  const terrain = determineTerrainType(noiseValues);

  // Cache the terrain type
  terrainTypeCache.set(`${worldX},${worldY}`, terrain);
}

function createTileTexture(terrain, x, y, worldX, worldY, tileCache, noiseMaps, app, detailContainer, detailCache) {
  const { light, dark, secondary } = TERRAIN_INFO.find((t) => t.type === terrain).colors;

  const baseColor = light ?? 0x000000;
  const secondColor = secondary;
  const darkColor = dark ?? baseColor;

  const tile = createTexturedTile(terrain, baseColor, secondColor, darkColor, tileCache, noiseMaps, app);
  tile.position.set(x * config.TILE_SIZE, y * config.TILE_SIZE);
  detailContainer.parent.addChildAt(tile, 0);  // Add tile to the main chunk container
  createTerrainDetail(terrain, detailCache, x, y, detailContainer, worldX, worldY,app);
}

export function generateChunk(chunkX, chunkY, tileCache, noiseMaps, app, chunks, simplex, worldContainer, detailCache, terrainTypeCache) {
  const chunkKey = `${chunkX},${chunkY}`;
  if (chunks[chunkKey]) return;

  const chunkContainer = new PIXI.Container();
  chunkContainer.position.set(chunkX * config.CHUNK_SIZE * config.TILE_SIZE, chunkY * config.CHUNK_SIZE * config.TILE_SIZE);

  // Create a separate container for details within the chunk
  const detailContainer = new PIXI.Container();
  chunkContainer.addChild(detailContainer);

  // First pass: Generate terrain types
  for (let x = 0; x < config.CHUNK_SIZE; x++) {
    for (let y = 0; y < config.CHUNK_SIZE; y++) {
      generateTile(chunkX, chunkY, x, y, simplex, terrainTypeCache);
    }
  }

  // Second pass: Add rivers
  addTerrainFeatures(chunkX, chunkY, terrainTypeCache, simplex);

  // Third pass: Create textures
  for (let x = 0; x < config.CHUNK_SIZE; x++) {
    for (let y = 0; y < config.CHUNK_SIZE; y++) {
      const worldX = chunkX * config.CHUNK_SIZE + x;
      const worldY = chunkY * config.CHUNK_SIZE + y;
      const terrain = terrainTypeCache.get(`${worldX},${worldY}`);
      createTileTexture(terrain, x, y, worldX, worldY, tileCache, noiseMaps, app, detailContainer, detailCache);
    }
  }

  worldContainer.addChild(chunkContainer);
  chunks[chunkKey] = chunkContainer;
}





export function unloadChunk(chunkX, chunkY, chunks, tileCache, detailCache, worldContainer) {
  const chunkKey = `${chunkX},${chunkY}`;
  const chunk = chunks[chunkKey];
  if (chunk) {
    // Remove chunk from world container
    worldContainer.removeChild(chunk);
    chunk.destroy({ children: true }); // Destroy all sprites and the container
    
    // Remove from caches
    delete chunks[chunkKey];
    tileCache.delete(chunkKey); // if tiles are cached by chunk
    detailCache.delete(chunkKey); // if details are cached by chunk
  }
}



// Update function to sort details within each chunk and also sort the chunks in the worldContainer
export function sortAllChunks(chunks, worldContainer) {
  // Sort details inside each chunk based on their x and y positions
  Object.values(chunks).forEach(chunk => {
    if (chunk.children.length > 1 && chunk.children[1] instanceof PIXI.Container) {
      const detailContainer = chunk.children[1];
      detailContainer.children.sort((a, b) => {
        // Sort primarily by y (north/south), and if y is equal, sort by x (east/west)
        if (a.position.y === b.position.y) {
          return a.position.x - b.position.x;
        }
        return a.position.y - b.position.y;
      });
    }
  });

  // Sort the chunks themselves inside the world container based on their positions
  worldContainer.children.sort((a, b) => {
    // Sort primarily by y (north/south), and if y is equal, sort by x (east/west)
    if (a.position.y === b.position.y) {
      return a.position.x - b.position.x;
    }
    return a.position.y - b.position.y;
  });
}
