// TerrainTextures.js
// Handles the creation of terrain textures and noise maps.

import * as PIXI from 'pixi.js';
import { createNoise2D } from 'simplex-noise';
import { generateVoronoi, generateWaterTexture, generateIceTexture, adjustColor, parseColorString } from './utils';
import { config } from './config';
import { TERRAIN_INFO } from './TerrainTypes';


export function generateTextureNoiseMaps(textureNoiseMaps) {
  const simplex = createNoise2D();
  const basicSimplexTexture = new Array(config.TILE_SIZE * config.TILE_SIZE);
  for (let y = 0; y < config.TILE_SIZE; y++) {
    for (let x = 0; x < config.TILE_SIZE; x++) {
      const noise = simplex(x * 0.1, y * 0.1);
      basicSimplexTexture[y * config.TILE_SIZE + x] = (noise + 1) / 2;
    }
  }

  const perlin = createNoise2D();
  const fractal = createNoise2D();
  const sandTexture = new Array(config.TILE_SIZE * config.TILE_SIZE);
  for (let y = 0; y < config.TILE_SIZE; y++) {
    for (let x = 0; x < config.TILE_SIZE; x++) {
      const baseNoise = perlin(x * 0.24, y * 0.24);
      const detailNoise = fractal(x * 1, y * 1);
      sandTexture[y * config.TILE_SIZE + x] = (baseNoise * 0.8 + detailNoise * 0.2 + 1) / 2;
    }
  }

  const voronoi = generateVoronoi(config.TILE_SIZE, config.TILE_SIZE, 250);
  const rockTexture = new Array(config.TILE_SIZE * config.TILE_SIZE);
  for (let y = 0; y < config.TILE_SIZE; y++) {
    for (let x = 0; x < config.TILE_SIZE; x++) {
      rockTexture[y * config.TILE_SIZE + x] = voronoi[y * config.TILE_SIZE + x];
    }
  }

  const waterTexture = generateWaterTexture(config.TILE_SIZE, config.TILE_SIZE);
  const iceTexture = generateIceTexture(config.TILE_SIZE, config.TILE_SIZE);

  textureNoiseMaps.push(basicSimplexTexture);
  textureNoiseMaps.push(sandTexture);
  textureNoiseMaps.push(rockTexture);
  textureNoiseMaps.push(waterTexture);
  textureNoiseMaps.push(iceTexture);
}

export function createTexturedTile(terrain, baseColor, secondColor,darkColor, tileCache, noiseMaps, app) {
    
    const color = parseColorString(baseColor);
  const cacheKey = color.toString(16).padStart(6, '0');
  if (tileCache.has(cacheKey)) {
    return new PIXI.Sprite(tileCache.get(cacheKey));
  }
  const textureType = TERRAIN_INFO.find((t) => t.type === terrain)?.textureType ?? 0;
  const noiseMap = noiseMaps[textureType];
  const graphics = new PIXI.Graphics();
  graphics.beginFill(color);
  graphics.drawRect(0, 0, config.TILE_SIZE, config.TILE_SIZE);
  graphics.endFill();
  for (let y = 0; y < config.TILE_SIZE; y++) {
    for (let x = 0; x < config.TILE_SIZE; x++) {
      const noise = noiseMap[y * config.TILE_SIZE + x];
      const adjustedColor = adjustColor(baseColor, secondColor,darkColor, noise);
      const waveX = Math.sin(y / 7.4) * 2.74;
      const waveY = Math.cos(x / 7.4) * 2.7;

      graphics.beginFill(adjustedColor, 1);
      graphics.drawRect(Math.round(x + waveX), Math.round(y + waveY), 1, 1);
      graphics.endFill();
    }
  }

  const texture = app.renderer.generateTexture(graphics, {
    scaleMode: PIXI.SCALE_MODES.NEAREST,
    resolution: 1,
  });
  tileCache.set(cacheKey, texture);

  return new PIXI.Sprite(texture);
}
