//playerState.js
//Updates the player state and handles player interactions 
import { enlargeTerrainDetail, removeTerrainDetail, TERRAIN_DETAILS, getChunkContainerForTile, placeTerrainDetail } from './TerrainDetails';
import { config } from './config';
import { parseColorString, createRadialGradientTexture } from './utils';
import { toast } from 'react-toastify'; // Import toast
import { ACHIVEMENTS } from './achivements';
const TEST = config.PLAYER_TEST_MODE;
const DETAIL_SIZE_SCALE = 5;

const initialSpeedStats = {
    swimming: TEST ? 1 : 0,
    climbing: TEST ? 1 :0.2,
    running: TEST ? 1.5: 1,
    crawling: TEST? 1: 0.02,
    blocked:  TEST? 1:0,
};
const terrainMovementTypes = {
    swimming: ['LAKE', 'DEEP LAKE', "OCEAN MIDDLE"],
    climbing: ['MOUNTAIN', 'MOUNTAINPEAK', "SNOWY MOUNTAIN", "SNOWY MOUNTAINPEAK"],
    running: ['GRASS', 'SAND', "DIRT", "ICE", "SNOWY GRASS", "SAVANNA", "OASIS", "SANDY SAVANNA",'FOREST', 'SNOW', "SNOW FOREST",],
    crawling:["QUICKSAND"],
    blocked: [],//["STATUE GROUND", "STATUE"],
};

export const DEFAULT_PLAYER_STATE = {
    mode: 'Grow',
    xp: 0,
    lastArrowPosition: null,
    color: config.PLAYER_BASE_COLOR,
    ...ACHIVEMENTS,
    abilities: {
        swimmingMajorBoost1: {
            "name": "Learn to swim",
            "unlocked": false,
            "price": 50,
            "description": "Learn to swim"
        },
    },
    enlargedDetails: {}, 
    deletedDetails: {},  
    maxDimensions: {} 
};


export function createPlayerState(playerStateRef, setPlayerState, player, setIsMenuOpen) {
    let modeIndex = 1;
    const allModes = ['Passive', 'Grow', 'Free', 'Destroy'];

    const modeColors = {
        Passive: config.PLAYER_BASE_COLOR,
        Grow: config.PLAYER_BASE_COLOR,
        Free: config.PLAYER_BASE_COLOR,//"#050996",
        Destroy: "#940721",
    };

    const setPlayerStateInternal = (newState) => {
        if (JSON.stringify(playerStateRef.current) !== JSON.stringify(newState)) {
            playerStateRef.current = newState; // Update the ref
            setPlayerState(newState); // Update the state
            updatePlayerColor(player, newState.color); // Update player color
            console.log('Player state updated:', newState);
        }
    };
    const updatePlayerStateInternal = (newState) => {
        let updatedState = { ...playerStateRef.current, ...newState };

        // Check if any achievements have been unlocked
        updatedState.achievements = checkAchievements(updatedState);

        // Update the player state with the new achievements and XP
        setPlayerStateInternal(updatedState);
    };
    const getTerrainGlideAdjustment  = (terrainType) => {
        switch(terrainType) {
            case 'ICE':
                return 1;
            default:
                return 0;
        }
    };

    const getTerrainSpeedAdjustment = (terrainType) => {
        const { abilities } = playerStateRef.current;
        const speedStats = { ...initialSpeedStats }; // Initialize with default speeds

        // Apply unlocked abilities to improve speed stats
        Object.keys(speedStats).forEach(type => {
            if (abilities[`${type}MinorBoost1`]?.unlocked) speedStats[type] += 0.1;
            if (abilities[`${type}MinorBoost2`]?.unlocked) speedStats[type] += 0.1;
            if (abilities[`${type}MajorBoost1`]?.unlocked) speedStats[type] += 0.5;
            if (abilities[`${type}MajorBoost2`]?.unlocked) speedStats[type] += 0.5;
        });

        // Exception for LAKE: always 0.5 higher than swimming speed
        if (terrainType === 'LAKE') {
            return speedStats['swimming'] + 0.5;
        }

        // Adjust speed based on terrain
        for (const [type, terrains] of Object.entries(terrainMovementTypes)) {
            if (terrains.includes(terrainType)) {
                return speedStats[type]; // Return the corresponding speed stat
            }
        }

        return speedStats["running"] ?? 1; // Default speed
    };
    const getBlockedTerrain = () => {
        const blockedTerrains = [];

        Object.keys(terrainMovementTypes).forEach(type => {
            terrainMovementTypes[type].forEach(terrain => {
                const terrainSpeed = getTerrainSpeedAdjustment(terrain);
                if (terrainSpeed <= 0) {
                    blockedTerrains.push(terrain); // Block terrain if speed is <= 0
                }
            });
        });

        return blockedTerrains;
    };


    const setMode = (newMode) => {
        if (allModes.includes(newMode)) {
            const newColor = modeColors[newMode];
            updatePlayerStateInternal({ mode: newMode, color: newColor });
        }
    };

    const cycleMode = () => {
        modeIndex = (modeIndex + 1) % allModes.length;
        setMode(allModes[modeIndex]);
        return allModes[modeIndex];
    };




    const handleTerrainInteraction = (tileX, tileY, caches) => {
        let xpGain = 0;

        switch (playerStateRef.current.mode) {
            case 'Grow':
            case 'Free': {
                const enlargedDetails = enlargeTerrainDetail(tileX, tileY, caches);
                if (enlargedDetails) {
                    enlargedDetails.forEach(({ detailKey, size }) => {
                        updateEnlargedDetailProgress(detailKey, playerStateRef,size);

                    });
                }
                trySpawnTerrainDetail(tileX, tileY, caches, playerStateRef, updatePlayerStateInternal)
                break;
            }
            case 'Destroy': {
                const removedDetails = removeTerrainDetail(tileX, tileY, caches);
                if (removedDetails) {

                    removedDetails.forEach(({ detailKey }) => {
                        // Use detailKey to track deleted terrain details in the state
                        const currentDeletedCount = playerStateRef.current.deletedDetails[detailKey] || 0;
                        playerStateRef.current.deletedDetails[detailKey] = currentDeletedCount + 1;
                    });
                }
                break;
            }
            case 'Passive':
                break;
            default:
                console.warn('Unknown player mode');
        }
        updatePlayerStateInternal();
    };



    const purchaseAbility = (abilityKey) => {
        const ability = playerStateRef.current.abilities[abilityKey];

        if (ability && !ability.unlocked && playerStateRef.current.xp >= ability.price) {
            const updatedXp = playerStateRef.current.xp - ability.price;
            const updatedAbilities = { ...playerStateRef.current.abilities, [abilityKey]: { ...ability, unlocked: true } };
            let newMode = playerStateRef.current.mode;
            if (abilityKey === 'freeModeAccess') {
                newMode = 'Free';
            }
            updatePlayerStateInternal({
                xp: updatedXp,
                abilities: updatedAbilities,
                mode: newMode,
                color: modeColors[newMode], // Update color if mode changes
            });

            console.log(`Ability purchased: ${ability.description}`);
        } else {
            console.log('Not enough XP to purchase this ability.');
        }
    };
    const openMenuOnClick = () => {
        setIsMenuOpen(true); // Toggle the menu visibility
    };
    const closeMenu = () => {
        setIsMenuOpen(false); // Close the menu
    };

    return {
        playerState: playerStateRef.current,
        allModes,
        setPlayerState: setPlayerStateInternal,
        setMode,
        getTerrainGlideAdjustment ,
        purchaseAbility,
        updatePlayerStateInternal,
        cycleMode,
        openMenuOnClick, // Add the openMenuOnClick function to the player object
        closeMenu,       // Add closeMenu to playerState
        getBlockedTerrain,
        handleTerrainInteraction,
        getTerrainSpeedAdjustment,
    };
}



function calculateAngle(currentX, currentY, targetX, targetY) {
    // Calculate the difference in coordinates
    const deltaX = targetX - currentX;
    const deltaY = targetY - currentY;
  
    // Calculate the angle in radians, relative to the positive X-axis
    let angleInRadians = Math.atan2(deltaX, -deltaY); // -deltaY makes 0 deg point upwards
  
    // Convert radians to degrees
    let angleInDegrees = angleInRadians * (180 / Math.PI) ;
    if (angleInDegrees < 0) {
        angleInDegrees += 350; // Convert negative angles to positive
    }
  console.log("Current X,Y: ", currentX, currentY, "Target X,Y: ", targetX, targetY, "Angle: ", angleInDegrees);
    return angleInDegrees;
  }
  
export function trySpawnTerrainDetail(tileX, tileY, caches, playerStateRef, updatePlayerStateInternal) {
    const { detailCache, chunks } = caches;
    const chunkContainer = getChunkContainerForTile(tileX, tileY, chunks);
    if (!chunkContainer) {
        return;
    }

    const chunkX = Math.floor(tileX / config.CHUNK_SIZE);
    const chunkY = Math.floor(tileY / config.CHUNK_SIZE);
    const relativeTileX = tileX - chunkX * config.CHUNK_SIZE;
    const relativeTileY = tileY - chunkY * config.CHUNK_SIZE;

    const key = `${tileX},${tileY}`;
    if (detailCache.has(key)) { return; }
    if (Math.random() > 0.005) { return; }

    // Determine the angle relative to the last arrow
    const lastArrowPosition = playerStateRef.current.lastArrowPosition;
    //const angle = calculateAngle(tileX, tileY,lastArrowPosition?.x ?? 0, lastArrowPosition?.y ?? 0);
    //UNTIL I HAVE TIME TO FIX THE ANGLE FUNCTION
    const angle = 0;


    // Place the arrow with the calculated angle in degrees
    const selectedDetail = 'arrow';
    placeTerrainDetail(selectedDetail, detailCache, relativeTileX, relativeTileY, chunkContainer, tileX, tileY, angle);

    // Update the playerState to store the position of the current arrow
    updatePlayerStateInternal({
        lastArrowPosition: { x: tileX, y: tileY }
    });
}

  


const updateEnlargedDetailProgress = (detailKey, playerStateRef, spriteSize) => {
    // Track enlarged detail count
    const size = {width: Math.round(spriteSize.width*DETAIL_SIZE_SCALE * 10) / 10, height: Math.round(spriteSize.height*DETAIL_SIZE_SCALE * 10) / 10};
    const currentEnlargedCount = playerStateRef.current.enlargedDetails[detailKey] || 0;
    playerStateRef.current.enlargedDetails[detailKey] = currentEnlargedCount + 1;

    // Track the tallest and widest sizes for this detailKey
    if (!playerStateRef.current.maxDimensions[detailKey]) {
        playerStateRef.current.maxDimensions[detailKey] = { width: size.width, height: size.height };
    } else {
        const currentMax = playerStateRef.current.maxDimensions[detailKey];
        playerStateRef.current.maxDimensions[detailKey] = {
            width: Math.max(currentMax.width, size.width),
            height: Math.max(currentMax.height, size.height),
        };
    }

    console.log(playerStateRef.current.enlargedDetails);
    console.log(playerStateRef.current.maxDimensions);
    console.log(playerStateRef.current);

    // Update achievement progress
    const achievements = playerStateRef.current.achievements || {};
    updateAchievementProgress(detailKey, achievements, size);
};

const updateAchievementProgress = (detailKey, achievements, size) => {
    Object.keys(achievements).forEach(achievementKey => {
        const achievement = achievements[achievementKey];
        
        if (achievementKey.includes(detailKey)) {
            // For counting-type achievements (e.g., grow 5 items)
            if (achievementKey.startsWith('enlarge1_')) {
                achievement.progress += 1;
            }
            
            // For largest-type achievements (e.g., grow to a certain size)
            if (achievementKey.startsWith('largest_')) {
                const largestDimension = Math.max(size.width, size.height);
                achievement.progress = Math.max(achievement.progress, largestDimension);
            }
        }
    });
};

//Checking achievements 
const checkAchievements = (playerState) => {
    const newAchievements = { ...playerState.achievements };
    Object.keys(newAchievements).forEach(achievementKey => {
        const achievement = newAchievements[achievementKey];
        
        if (achievement && !achievement.unlocked) {
            // Check if achievement goal has been reached for either progress or largest-type
            if (achievement.progress >= achievement.progressGoal) {
                toast(`Achievement Unlocked: ${achievement.name}`);
                achievement.unlocked = true;
                playerState.xp += achievement.reward;
                console.log(`Achievement Unlocked: ${achievement.name}`);
            }
        }
    });

    return newAchievements;
};








