function preprocessTerrainInfo(terrainInfo, noiseMapsInfo) {
    const processed = new Array(noiseMapsInfo.length).fill().map(() => []);

    terrainInfo.forEach((terrain, index) => {
        terrain.thresholds.forEach((rule) => {
            const highestNoiseMap = Math.max(...Object.keys(rule).map(Number));
            const conditionCount = Object.keys(rule).length;

            processed[highestNoiseMap].push({
                type: terrain.type,
                rule,
                conditionCount,
                originalIndex: index,
            });
        });
    });

    processed.forEach((noiseMapRules) => {
        noiseMapRules.sort((a, b) => {
            if (a.conditionCount !== b.conditionCount) {
                return b.conditionCount - a.conditionCount;
            }
            return a.originalIndex - b.originalIndex;
        });
    });

    return processed;
}

const processedTerrainInfo = preprocessTerrainInfo(TERRAIN_INFO, noise_maps_info);

export function determineTerrainType(noiseValues) {
    for (let noiseMap = noise_maps_info.length - 1; noiseMap >= 0; noiseMap--) {
        for (const { type, rule } of processedTerrainInfo[noiseMap]) {
            if (
                Object.entries(rule).every(
                    ([noiseIndex, [min, max]]) => noiseValues[noiseIndex] >= min && noiseValues[noiseIndex] <= max
                )
            ) {
                return type;
            }
        }
    }
    return 'GRASS';
}




export function generateNoiseValues(x, y, simplex) {
    return noise_maps_info.map((info) => {
        if (info.noiseType === 'simplex') {
            return simplex(x * info.scale + info.offsetX, y * info.scale + info.offsetY);
        } else if (info.noiseType === 'worley') {
            return generateWorleyNoise(x * info.scale + info.offsetX, y * info.scale + info.offsetY);
        } else if (info.noiseType === 'chunk') {
            // Handle chunk noise type here
            return generateChunkNoise(x + info.offsetX, y + info.offsetY, info.chunkDirection);
        }
    });
}