import { config } from './config';
import { generateNoiseValues } from './TerrainTypes';

const RIVER_START_CHANCE = 0.1; // 30% chance for a mountain tile to start a river
const MAX_RIVER_LENGTH = 25; // Maximum river length to prevent infinite loops

const STATUE_START_CHANCE = 0.001; // 1% chance for a forest tile to start a statue area
const STATUE_TERRAIN_ORIGIN = 'GRASS'; // Replace 'FOREST' with any terrain type you need
const STATUE_SIZE = 5; // The size of the statue (7x7)
const BUFFER = 1; // Extra space around the statue


export function addTerrainFeatures(chunkX, chunkY, terrainTypeCache, simplex) {
  // Add terrain features like rivers, statues, etc.
  // addRivers(chunkX, chunkY, terrainTypeCache, simplex);
  addStatue(chunkX, chunkY, terrainTypeCache);
}




function addStatue(chunkX, chunkY, terrainTypeCache) {
  for (let x = 0; x < config.CHUNK_SIZE; x++) {
    for (let y = 0; y < config.CHUNK_SIZE; y++) {
      const worldX = chunkX * config.CHUNK_SIZE + x;
      const worldY = chunkY * config.CHUNK_SIZE + y;
      const terrain = terrainTypeCache.get(`${worldX},${worldY}`);

      // Start the statue if the terrain matches and chance is met
      if (([STATUE_TERRAIN_ORIGIN]?.includes(terrain)) && Math.random() < STATUE_START_CHANCE) {
        // Ensure there's enough space and the surrounding terrain is valid before generating the statue
        if (isValidStatueLocation(x, y, chunkX, chunkY, terrainTypeCache)) {
          generateStatue(x, y, chunkX, chunkY, terrainTypeCache);
        }
      }
    }
  }
}

function isValidStatueLocation(startX, startY, chunkX, chunkY, terrainTypeCache) {
  const halfSize = Math.floor(STATUE_SIZE / 2);

  // Check if the statue fits within the chunk including the buffer
  if (startX - halfSize - BUFFER < 0 || startX + halfSize + BUFFER >= config.CHUNK_SIZE ||
    startY - halfSize - BUFFER < 0 || startY + halfSize + BUFFER >= config.CHUNK_SIZE) {
    return false;
  }

  // Check that there are at least two valid terrain tiles in all four directions
  const requiredDistance = 2;
  for (let dx = -requiredDistance; dx <= requiredDistance; dx++) {
    if (!isTerrainTile(startX + dx, startY, chunkX, chunkY, terrainTypeCache, [STATUE_TERRAIN_ORIGIN])) {
      return false;
    }
  }
  for (let dy = -requiredDistance; dy <= requiredDistance; dy++) {
    if (!isTerrainTile(startX, startY + dy, chunkX, chunkY, terrainTypeCache, [STATUE_TERRAIN_ORIGIN])) {
      return false;
    }
  }

  return true;
}
function isTerrainTile(x, y, chunkX, chunkY, terrainTypeCache, validTerrainTypes) {
  // Check if the tile at (x, y) matches any of the valid terrain types and is inside the chunk bounds
  if (x < 0 || x >= config.CHUNK_SIZE || y < 0 || y >= config.CHUNK_SIZE) {
    return false;
  }
  const worldX = chunkX * config.CHUNK_SIZE + x;
  const worldY = chunkY * config.CHUNK_SIZE + y;
  const terrain = terrainTypeCache.get(`${worldX},${worldY}`);

  // Check if the terrain type is in the list of valid terrain types
  return validTerrainTypes.includes(terrain);
}


function generateStatue(startX, startY, chunkX, chunkY, terrainTypeCache) {
  const halfSize = Math.floor(STATUE_SIZE / 2);

  // Replace a STATUE_SIZExSTATUE_SIZE area with "STATUE GROUND"
  for (let dx = -halfSize; dx <= halfSize; dx++) {
    for (let dy = -halfSize; dy <= halfSize; dy++) {
      const x = startX + dx;
      const y = startY + dy;

      // Ensure we're inside chunk bounds (although this is already checked earlier)
      if (x >= 0 && x < config.CHUNK_SIZE && y >= 0 && y < config.CHUNK_SIZE) {
        const worldX = chunkX * config.CHUNK_SIZE + x;
        const worldY = chunkY * config.CHUNK_SIZE + y;

        // Skip the corners to make the shape more rounded
        if (Math.abs(dx) === halfSize && Math.abs(dy) === halfSize) {
          continue; // Skip corners
        }

        if (dx === 0 && dy === 0) {
          // Set the center tile as "STATUE"
          terrainTypeCache.set(`${worldX},${worldY}`, 'STATUE');
        } else {
          // Set surrounding tiles as "STATUE GROUND"
          terrainTypeCache.set(`${worldX},${worldY}`, 'STATUE GROUND');
        }
      }
    }
  }
}
function getNeighbors(x, y, chunkX, chunkY) {
  const neighbors = [
    { x: x - 1, y: y },
    { x: x + 1, y: y },
    { x: x, y: y - 1 },
    { x: x, y: y + 1 },
  ];

  return neighbors.filter(n =>
    n.x >= 0 && n.x < config.CHUNK_SIZE &&
    n.y >= 0 && n.y < config.CHUNK_SIZE
  );
}

function isWaterTile(terrain) {
  return ['LAKE', 'DEEP LAKE', 'OCEAN MIDDLE'].includes(terrain);
}

export function addRivers(chunkX, chunkY, terrainTypeCache, simplex) {
  for (let x = 0; x < config.CHUNK_SIZE; x++) {
    for (let y = 0; y < config.CHUNK_SIZE; y++) {
      const worldX = chunkX * config.CHUNK_SIZE + x;
      const worldY = chunkY * config.CHUNK_SIZE + y;
      const terrain = terrainTypeCache.get(`${worldX},${worldY}`);

      if (terrain === 'MOUNTAINPEAK' && Math.random() < RIVER_START_CHANCE) {
        generateRiver(x, y, chunkX, chunkY, terrainTypeCache, simplex);
      }
    }
  }
}

function generateRiver(startX, startY, chunkX, chunkY, terrainTypeCache, simplex) {
  let x = startX;
  let y = startY;
  let length = 0;

  while (length < MAX_RIVER_LENGTH) {
    const worldX = chunkX * config.CHUNK_SIZE + x;
    const worldY = chunkY * config.CHUNK_SIZE + y;
    const currentTerrain = terrainTypeCache.get(`${worldX},${worldY}`);

    if (isWaterTile(currentTerrain)) {
      // River has reached a water tile, so we're done
      break;
    }

    terrainTypeCache.set(`${worldX},${worldY}`, 'RIVER');

    const neighbors = getNeighbors(x, y, chunkX, chunkY);
    if (neighbors.length === 0) break;

    // Choose the next tile based on elevation (noise map 0)
    const nextTile = chooseNextTile(neighbors, chunkX, chunkY, simplex);
    x = nextTile.x;
    y = nextTile.y;
    length++;
  }
}

function chooseNextTile(neighbors, chunkX, chunkY, simplex) {
  const tiles = neighbors.map(n => {
    const worldX = chunkX * config.CHUNK_SIZE + n.x;
    const worldY = chunkY * config.CHUNK_SIZE + n.y;

    // Generate noise values for this position
    const noiseValues = generateNoiseValues(worldX, worldY, simplex);

    // Use the first noise value (assuming elevation is based on the first noise component)
    const elevation = noiseValues[0];


    // Score combines elevation (lower is better for river) and the snaking factor for random variation
    const score = elevation * 0.8 + Math.random() * 0.2;

    return { ...n, score };
  });

  // Sort by the score, including a slight randomness to break ties
  tiles.sort((a, b) => (a.score + Math.random() * 0.05) - (b.score + Math.random() * 0.05));

  // Choose the tile with the best score (first in the sorted array)
  return tiles[0];
}