// drawTerrain.js
import * as PIXI from 'pixi.js';

// Define a constant margin for drawing outside the canvas
const DRAW_MARGIN = 100;  // Modify this value as needed

// Function to check if the sprite's position is within the canvas plus the margin
export const isWithinDrawMargin = (drawX, drawY, canvasWidth, canvasHeight, margin = DRAW_MARGIN) => {
  return (
    drawX >= -margin && drawX <= canvasWidth + margin &&
    drawY >= -margin && drawY <= canvasHeight + margin
  );
};

const drawTerrain = (pixiApp, tiles, terrainSettings, config, worldCenter, terrainSprites, zoom, activeSprites) => {
  const canvasWidth = pixiApp.renderer.width;
  const canvasHeight = pixiApp.renderer.height;

  const visibleSprites = new Map(); // New map to track currently visible sprites

  Object.keys(tiles).forEach((chunkKey) => {
    const [chunkX, chunkY] = chunkKey.split(',').map(Number);

    tiles[chunkKey].forEach((tile) => {
      // Adjust behavior for zoom level < 0.2
      let tileSize = config.tileSize * zoom;
      let skipTile = false;

      if (zoom < 0.2) {
        tileSize = config.tileSize * zoom * 2; // Adjust tile size to be twice its calculated zoom size
        
        // Skip every other tile (both x and y must be even for the tile to be drawn)
        if ((tile.x % 2 !== 0) || (tile.y % 2 !== 0)) {
          skipTile = true;
        }
      }

      if (skipTile) {
        return; // Skip this tile
      }

      const terrainSprite = terrainSprites?.find(t => t.type === tile.terrainType)?.texture;

      const globalX = (chunkX * config.chunkSize) + (tile.x - (chunkX * config.chunkSize));
      const globalY = (chunkY * config.chunkSize) + (tile.y - (chunkY * config.chunkSize));
      const drawX = (globalX - worldCenter.x) * config.tileSize * zoom;
      const drawY = (globalY - worldCenter.y) * config.tileSize * zoom;

      const spriteKey = `${globalX},${globalY}`; // Unique key for each sprite

      // Check if the sprite should be visible
      if (isWithinDrawMargin(drawX, drawY, canvasWidth, canvasHeight, DRAW_MARGIN)) {
        let sprite = activeSprites.get(spriteKey);
        if (!sprite) {
          // Create sprite if it doesn't exist
          if (terrainSprite && zoom > 0.3) {
            sprite = new PIXI.Sprite(terrainSprite.texture);
            sprite.scale.set(zoom);
          } else {
            sprite = new PIXI.Graphics();
            const terrainColor = terrainSettings?.find(t => t.type === tile.terrainType)?.colors.light ?? 0x808080;
            sprite.beginFill(terrainColor);
            sprite.drawRect(0, 0, tileSize, tileSize); // Use the adjusted tileSize
            sprite.endFill();
          }

          sprite.position.set(drawX, drawY);
          pixiApp.stage.addChild(sprite);
          activeSprites.set(spriteKey, sprite);  // Track the sprite
        } else {
          // Move existing sprite
          sprite.position.set(drawX, drawY);
          sprite.scale.set(zoom);
        }

        // Mark this sprite as visible in this frame
        visibleSprites.set(spriteKey, sprite);
      }
    });
  });

  // Remove sprites that are no longer visible
  activeSprites.forEach((sprite, key) => {
    if (!visibleSprites.has(key)) {
      pixiApp.stage.removeChild(sprite);
      activeSprites.delete(key);
    }
  });
};

export default drawTerrain;
