// World.js
// Handles world generation, including terrain and chunks.

import * as PIXI from 'pixi.js';
import { createNoise2D } from 'simplex-noise';
import { generateTerrainDetails } from './TerrainDetails';
import { generateTextureNoiseMaps } from './TerrainTextures';
import { generateChunk, sortAllChunks } from './Terrain';
import { config } from './config';

export function setupWorld(app, worldContainer, player) {
    const simplex = createNoise2D();
    const textureNoiseMaps = [];
    const chunks = {};
    const terrainTypeCache = new Map();
    const tileCache = new Map();
    
    const detailCache = new Map();

    function generateWorld() {
        generateTextureNoiseMaps(textureNoiseMaps);
        generateTerrainDetails(detailCache);

        const centerChunkX = Math.floor(config.VISIBLE_TILES / 2 / config.CHUNK_SIZE);
        const centerChunkY = Math.floor(config.VISIBLE_TILES / 2 / config.CHUNK_SIZE);

        for (let x = -1; x <= 1; x++) {
            for (let y = -1; y <= 1; y++) {
                generateChunk(
                    centerChunkX + x,
                    centerChunkY + y,
                    tileCache,
                    textureNoiseMaps,
                    app,
                    chunks,
                    simplex,
                    worldContainer,
                    detailCache,
                    terrainTypeCache
                );
            }
        }

        // Sort all chunks after generation
        sortAllChunks(chunks, worldContainer);
    }

    generateWorld();

    return { tileCache, textureNoiseMaps, chunks, simplex, detailCache, terrainTypeCache };
}