import React from 'react';
import { TERRAIN_DETAILS } from './TerrainDetails';

export default function PlayerMenu({ playerState, playerRef, isMenuOpen, setIsMenuOpen }) {
    const player = playerRef.current;

    const handlePurchaseAbility = (abilityKey) => {
        player.purchaseAbility(abilityKey); // Call the purchase ability function
    };

    return (
        <>
            {!isMenuOpen && (
                <div className='top-bar'>
                    {/* <div className='xp-info'>XP: {playerState.xp}</div> */}
                    <div className='menu-button' onClick={() => setIsMenuOpen(true)}>
                        <div className="square-container">
                            <div style={{backgroundImage: `url(${process.env.PUBLIC_URL + '/images/menuButton.png'})`}} className='square square-1'></div>
                            <div style={{backgroundImage: `url(${process.env.PUBLIC_URL + '/images/menuButton.png'})`}} className='square square-2'></div>
                            <div style={{backgroundImage: `url(${process.env.PUBLIC_URL + '/images/menuButton.png'})`}} className='square square-4'></div>
                            

                        </div>
                    </div>

                </div>
            )}
            {isMenuOpen && (
                <div className="menu-overlay">
                    <div className="menu">
                        <button className="close-button" onClick={player?.closeMenu}>&times;</button>
                        {/* XP Tracker */}
                        <div className="xp-tracker">
                            <p className="xp-amount">{playerState.xp} Grow Energy</p>
                        </div>


                        {/* Achievements Section */}
                        <h2 className="section-title">Achievements</h2>
                        <div className="menu-section">

                            <div className="grid-container">
                                {playerState.achievements && Object.keys(playerState.achievements).map((key) => {
                                    const achievement = playerState.achievements[key];
                                    const detailKey = key.split('_')[1]; // Extract detailKey from the achievement key
                                    const detail = TERRAIN_DETAILS[detailKey]; // Get the corresponding terrain detail (if exists)

                                    return (
                                        <div className={`card ${achievement.unlocked ? 'unlocked' : ''}`} key={key}>
                                            {detail && detail.content && (
                                                <img
                                                    src={detail.content}
                                                    alt={detail.name}
                                                    className="achievement-image"
                                                />
                                            )}
                                            <div className="card-title">{achievement.name}</div>
                                            <div className="card-description">{achievement.description}</div>
                                            <div className="bottom-info">
                                                <div className="card-reward">Reward: {achievement.reward}</div>
                                                <div className={`card-progress ${achievement.unlocked ? 'progress-unlocked' : ''}`}>
                                                    {achievement.unlocked ? 'Unlocked! ' : ""}{`${Math.round(achievement.progress * 1) / 1}/${achievement.progressGoal}`}
                                                </div>
                                            </div>
                                        </div>
                                    );
                                })}
                            </div>
                        </div>




                        <div className="divider"></div>



                        <div className="divider"></div>
                        {/* Abilities Section */}
                        <h2 className="section-title">Abilities</h2>
                        <div className="menu-section abilities">

                            <div className="grid-container">
                                {playerState.abilities && Object.keys(playerState.abilities).map((key) => {
                                    const ability = playerState.abilities[key];
                                    return (
                                        <div className="card" key={key}>
                                            <div className="card-title">{ability.description}</div>
                                            <div className="card-cost">Cost: {ability.price} Energy</div>
                                            <button
                                                className='card-button'
                                                onClick={() => handlePurchaseAbility(key)}
                                                disabled={playerState.xp < ability.price}
                                            >
                                                {ability.unlocked ? 'Unlocked' : 'Buy'}
                                            </button>
                                        </div>
                                    );
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
}
