import * as PIXI from "pixi.js";

// Constants
const JOINT_COUNT = 48;
const LINK_SIZE = 8;
const ANGLE_CONSTRAINT = Math.PI / 8;
const HEAD_RADIUS = 22;
const MAX_BODY_WIDTH = 24;
const MIN_BODY_WIDTH = 8;
const EYE_OFFSET = 22;
const EYE_SIZE = 5;
const LINE_WIDTH = 1;
const SPEED = 1.5;
const DIRECTION_CHANGE_PROBABILITY = 0.01; // Small probability to change direction
const RANDOM_POINT_SPEED = 3; // Movement speed

class Chain {
    constructor(app, origin) {
        this.app = app;
        this.linkSize = LINK_SIZE;
        this.angleConstraint = ANGLE_CONSTRAINT;
        this.joints = [];
        this.angles = [];

        for (let i = 0; i < JOINT_COUNT; i++) {
            this.joints.push(new PIXI.Point(origin.x, origin.y + i * LINK_SIZE));
            this.angles.push(0);
        }

        this.graphics = new PIXI.Graphics();
        this.app.stage.addChild(this.graphics);
    }

    resolve(targetPos) {
        this.angles[0] = Math.atan2(targetPos.y - this.joints[0].y, targetPos.x - this.joints[0].x);
        this.joints[0].set(targetPos.x, targetPos.y);

        for (let i = 1; i < this.joints.length; i++) {
            const curAngle = Math.atan2(this.joints[i - 1].y - this.joints[i].y, this.joints[i - 1].x - this.joints[i].x);
            this.angles[i] = this.constrainAngle(curAngle, this.angles[i - 1], this.angleConstraint);
            const newPos = new PIXI.Point(
                this.joints[i - 1].x - Math.cos(this.angles[i]) * LINK_SIZE,
                this.joints[i - 1].y - Math.sin(this.angles[i]) * LINK_SIZE
            );
            this.joints[i].copyFrom(newPos);
        }
    }

    constrainAngle(angle, anchor, constraint) {
        const diff = this.relativeAngleDiff(angle, anchor);
        if (Math.abs(diff) <= constraint) {
            return this.simplifyAngle(angle);
        }
        return this.simplifyAngle(anchor + Math.sign(diff) * constraint);
    }

    relativeAngleDiff(angle, anchor) {
        angle = this.simplifyAngle(angle + Math.PI - anchor);
        anchor = Math.PI;
        return anchor - angle;
    }

    simplifyAngle(angle) {
        return angle - Math.floor(angle / (2 * Math.PI)) * 2 * Math.PI;
    }

    display() {
        this.graphics.clear();
        this.graphics.lineStyle(LINE_WIDTH, 0xFFFFFF);
        this.graphics.beginFill(0xAC3931);

        const headAngle = this.angles[0];
        const headX = this.joints[0].x;
        const headY = this.joints[0].y;

        // Start path from the right side of the head
        this.graphics.moveTo(
            headX + Math.cos(headAngle + Math.PI / 2) * HEAD_RADIUS,
            headY + Math.sin(headAngle + Math.PI / 2) * HEAD_RADIUS
        );

        // Draw body
        const bodyPoints = [];
        for (let i = 1; i < this.joints.length; i++) {
            const t = (i - 1) / (this.joints.length - 2);
            const width = this.getBodyWidth(t);
            const angle = this.angles[i];
            
            bodyPoints.push(
                new PIXI.Point(
                    this.joints[i].x + Math.cos(angle + Math.PI / 2) * width,
                    this.joints[i].y + Math.sin(angle + Math.PI / 2) * width
                ),
                new PIXI.Point(
                    this.joints[i].x + Math.cos(angle - Math.PI / 2) * width,
                    this.joints[i].y + Math.sin(angle - Math.PI / 2) * width
                )
            );
        }

        // Draw right side of body
        for (let i = 0; i < bodyPoints.length; i += 2) {
            this.graphics.lineTo(bodyPoints[i].x, bodyPoints[i].y);
        }

        // Draw pointy tail
        const lastJoint = this.joints[this.joints.length - 1];
        const tailAngle = this.angles[this.angles.length - 1];
        this.graphics.lineTo(lastJoint.x, lastJoint.y);

        // Draw left side of body
        for (let i = bodyPoints.length - 1; i >= 1; i -= 2) {
            this.graphics.lineTo(bodyPoints[i].x, bodyPoints[i].y);
        }

        // Draw head
        const headStartAngle = headAngle - Math.PI / 2;
        const headEndAngle = headAngle + Math.PI / 2;
        this.graphics.arc(headX, headY, HEAD_RADIUS, headStartAngle, headEndAngle);

        this.graphics.closePath();
        this.graphics.endFill();

        // Draw eyes
        this.graphics.beginFill(0xFFFFFF);
        const eyeAngle = headAngle + Math.PI / 2;
        this.graphics.drawCircle(
            headX + Math.cos(eyeAngle) * EYE_OFFSET,
            headY + Math.sin(eyeAngle) * EYE_OFFSET,
            EYE_SIZE
        );
        this.graphics.drawCircle(
            headX + Math.cos(eyeAngle - Math.PI) * EYE_OFFSET,
            headY + Math.sin(eyeAngle - Math.PI) * EYE_OFFSET,
            EYE_SIZE
        );
        this.graphics.endFill();
    }

    getBodyWidth(t) {
        // Quadratic function to make the body widest around 1/4 of its length
        const peak = 0.25;
        const a = -4 * (MAX_BODY_WIDTH - MIN_BODY_WIDTH);
        const b = 4 * (MAX_BODY_WIDTH - MIN_BODY_WIDTH) * peak;
        const c = MAX_BODY_WIDTH;
        return Math.max(a * (t - peak) * (t - peak) + b * (t - peak) + c, 1);
    }
}

export const manageDrawings = (app) => {
    const snake = new Chain(app, new PIXI.Point(app.screen.width / 2, app.screen.height / 2));
    let targetX = getRandomPoint(app.screen.width);
    let targetY = getRandomPoint(app.screen.height);
    
    let directionX = Math.random() - 0.5; // Initial random direction on X axis
    let directionY = Math.random() - 0.5; // Initial random direction on Y axis
    

    app.ticker.add(() => {
        const dx = targetX - snake.joints[0].x;
        const dy = targetY - snake.joints[0].y;
        const distance = Math.sqrt(dx * dx + dy * dy);

        // Adjust the target point slightly towards a new random direction
        if (Math.random() < DIRECTION_CHANGE_PROBABILITY) {
            directionX = Math.random() - 0.5; // Change direction randomly
            directionY = Math.random() - 0.5;
        }

        targetX += directionX * RANDOM_POINT_SPEED; // Move the target point slightly
        targetY += directionY * RANDOM_POINT_SPEED;

        // Keep target within screen bounds
        targetX = Math.max(0, Math.min(app.screen.width, targetX));
        targetY = Math.max(0, Math.min(app.screen.height, targetY));

        const newHeadX = snake.joints[0].x + (dx / distance) * SPEED;
        const newHeadY = snake.joints[0].y + (dy / distance) * SPEED;

        snake.resolve(new PIXI.Point(newHeadX, newHeadY));
        snake.display();
    });
};

// Helper function to get a random point within a range
const getRandomPoint = (maxValue) => {
    return Math.random() * maxValue;
};
