// utils.js
// Utility functions used across the project.

import * as PIXI from 'pixi.js';
import { createNoise2D } from 'simplex-noise';
import { config } from './config';

export function parseColorString(colorStr) {
    // Handle 0x type color format
    if (typeof colorStr === 'number' || (typeof colorStr === 'string' && colorStr.startsWith('0x'))) {
      return typeof colorStr === 'number' ? colorStr : parseInt(colorStr.slice(2), 16);
    }
  
    // Handle RGB format
    if (typeof colorStr === 'string' && colorStr.startsWith('rgb')) {
      const rgbValues = colorStr.match(/\d+/g).map(Number);
      return (rgbValues[0] << 16) | (rgbValues[1] << 8) | rgbValues[2];
    }

    // Handle # format
    if (typeof colorStr === 'string' && colorStr.startsWith('#')) {
      let hex = colorStr.slice(1);
      if (hex.length === 3) {
        hex = hex
          .split('')
          .map((char) => char + char)
          .join('');
      }
      return parseInt(hex, 16);
    }
  
    throw new Error('Unsupported color format: ' + colorStr);
  }

export function createRadialGradientTexture(size, color, alpha) {
    const canvas = document.createElement('canvas');
    canvas.width = size * 2;
    canvas.height = size * 2;
    const ctx = canvas.getContext('2d');

    const gradient = ctx.createRadialGradient(size, size, 0, size, size, size);
    const rgbaColor = `rgba(${(color >> 16) & 255}, ${(color >> 8) & 255}, ${color & 255}, ${alpha})`;

    gradient.addColorStop(0, rgbaColor);
    gradient.addColorStop(1, `rgba(${(color >> 16) & 255}, ${(color >> 8) & 255}, ${color & 255}, 0)`);

    ctx.fillStyle = gradient;
    ctx.beginPath();
    ctx.arc(size, size, size, 0, Math.PI * 2);
    ctx.fill();

    return PIXI.Texture.from(canvas);
}

export function adjustColor(inputBaseColor, inputSecondColor, inputDarkColor, noise) {

    // Extract RGB components from baseColor and darkColor
    //use parseColorString
    const baseColor = parseColorString(inputBaseColor);
    const darkColor = parseColorString(inputDarkColor);
    const secondColor = inputSecondColor ? parseColorString(inputSecondColor) : inputBaseColor;
    let rBase = (baseColor >> 16) & 0xff;
    let gBase = (baseColor >> 8) & 0xff;
    let bBase = baseColor & 0xff;

    let rDark = (darkColor >> 16) & 0xff;
    let gDark = (darkColor >> 8) & 0xff;
    let bDark = darkColor & 0xff;

    let r, g, b;

    // Check if noise is above 0.5, use secondColor if it is
    if (noise > 0.75 && secondColor !== undefined) {
        r = (secondColor >> 16) & 0xff;
        g = (secondColor >> 8) & 0xff;
        b = secondColor & 0xff;
    } else {
        // Blend between baseColor and darkColor based on noise
        r = Math.floor(rBase * noise + rDark * (1.1 - noise));
        g = Math.floor(gBase * noise + gDark * (1.1 - noise));
        b = Math.floor(bBase * noise + bDark * (1.1 - noise));
    }

    // Apply brightness adjustment factor
    const factor = 0.95 + noise * 0.1;
    const newR = Math.min(255, Math.max(0, Math.floor(r * factor)));
    const newG = Math.min(255, Math.max(0, Math.floor(g * factor)));
    const newB = Math.min(255, Math.max(0, Math.floor(b * factor)));

    // Return the combined color
    return (newR << 16) | (newG << 8) | newB;
}


export function generateWorleyNoise(x, y, scale, numPoints = 100) {
    const points = [];
    for (let i = 0; i < numPoints; i++) {
        points.push({
            x: Math.random() * scale,
            y: Math.random() * scale,
        });
    }

    let minDist = Infinity;
    for (let point of points) {
        const dx = x - point.x;
        const dy = y - point.y;
        const dist = Math.sqrt(dx * dx + dy * dy);
        if (dist < minDist) {
            minDist = dist;
        }
    }

    // Normalize and shift the range from -1 to 1
    return (minDist / scale) * 2 - 1;
}

// Function to generate chunk-based noise with gradient and chunkdistance
export function generateChunkNoise(x, y, chunkDirection) {
    // Calculate chunk coordinates as gradient values +Math.random()*0.1  +(Math.random()-0.5)*0
    const chunkX = x / config.CHUNK_SIZE;
    const chunkY = y / config.CHUNK_SIZE;
    if(x === 0 && y === 0){    console.log(chunkX, chunkY);}

    if (chunkDirection === 'x') {
        return chunkX; // Gradient based on X coordinate
    } else if (chunkDirection === 'y') {
        return chunkY; // Gradient based on Y coordinate
    } else if (chunkDirection === 'chunkdistance') {
        // Absolute sum of X and Y chunk positions for chunkdistance
        return Math.abs(chunkX) + Math.abs(chunkY)+(Math.random()-0.5)*0.15; 
    }

    return 0; // Default case
}

export function generateVoronoi(width, height, numPoints) {
    const points = [];
    for (let i = 0; i < numPoints; i++) {
        points.push({
            x: Math.random() * width,
            y: Math.random() * height,
        });
    }

    const voronoi = new Array(width * height);
    for (let y = 0; y < height; y++) {
        for (let x = 0; x < width; x++) {
            let minDist = Infinity;
            let closestPointIndex = 0;
            for (let i = 0; i < points.length; i++) {
                const dx = x - points[i].x;
                const dy = y - points[i].y;
                const dist = dx * dx + dy * dy;
                if (dist < minDist) {
                    minDist = dist;
                    closestPointIndex = i;
                }
            }
            voronoi[y * width + x] = (closestPointIndex / (numPoints - 1)) * 2 - 1;
        }
    }
    return voronoi;
}

export function generateWaterTexture(width, height) {
    const waterTexture = new Array(width * height);
    const simplex = createNoise2D();

    for (let y = 0; y < height; y++) {
        for (let x = 0; x < width; x++) {
            let value = Math.sin(y * 3.0) * 0.5;
            value += Math.sin(y * 6.0) * 0.3;
            value += Math.sin(y * 10.0) * 0.15;
            value += Math.sin(y * 15.0 + x * 0.5) * 0.1;

            value += Math.sin(x * 2.0 + y * 0.2) * 0.05;

            const noise1 = simplex(x * 0.1, y * 0.1) * 0.3;
            const noise2 = simplex(x * 0.05 + 100, y * 0.5) * 0.15;
            value += noise1 + noise2;

            value = Math.tanh(value * 1.5);

            waterTexture[y * width + x] = value;
        }
    }

    return waterTexture;
}

export function generateIceTexture(width, height) {
    const iceTexture = new Array(width * height);
    const simplex = createNoise2D();

    for (let y = 0; y < height; y++) {
        for (let x = 0; x < width; x++) {
            let value = Math.sin(y * 0.1) * 0.3;
            value += Math.sin(y * 0.2) * 0.2;
            value += Math.sin(y * 0.05) * 0.1;

            const noise = simplex(y * 0.01, x * 0.001);
            value += noise * 0.5;

            value = ((value + 1) * 0.5) * 2 - 1;

            iceTexture[y * width + x] = value;
        }
    }

    return iceTexture;
}

export function lerp(a, b, t) {
    return a + (b - a) * t;
}
