// noiseMaps.js
import { createNoise2D } from 'simplex-noise';
const noise2D = createNoise2D();


export function generateNoiseValues(chunkX, chunkY, chunkSize, noiseMapsInfo) {
  const noiseMap = [];
  const startX = chunkX * chunkSize;
  const startY = chunkY * chunkSize;

  // Iterate over the chunk coordinates
  for (let y = 0; y < chunkSize; y++) {
    for (let x = 0; x < chunkSize; x++) {
      const worldX = startX + x;
      const worldY = startY + y;

      // Generate noise values for each noise map
      const noiseValues = noiseMapsInfo.map((info) => {
        if (info.noiseType === 'simplex') {
          return noise2D(worldX * info.scale + info.offsetX, worldY * info.scale + info.offsetY);
        } else if (info.noiseType === 'worley') {
          return generateWorleyNoise(worldX * info.scale + info.offsetX, worldY * info.scale + info.offsetY);
        } else if (info.noiseType === 'chunk') {
          return generateChunkNoise(worldX + info.offsetX, worldY + info.offsetY, info.chunkDirection, chunkSize);
        }
      });

      noiseMap.push({
        x: worldX,
        y: worldY,
        noiseValues,  // An array of noise values from all maps
      });
    }
  }

  return noiseMap;
}


export const generateNoiseMap = (chunkSize, chunkX, chunkY, scale = 0.1) => {
  const noiseMap = [];

  // Calculate the starting world coordinates for this chunk
  const startX = chunkX * chunkSize;
  const startY = chunkY * chunkSize;

  for (let y = 0; y < chunkSize; y++) {
    for (let x = 0; x < chunkSize; x++) {
      const worldX = startX + x;  // Adjust worldX to start at chunk's position
      const worldY = startY + y;  // Adjust worldY to start at chunk's position
      const value = noise2D(worldX * scale, worldY * scale);
      noiseMap.push({
        x: worldX,  // Store world coordinates
        y: worldY,
        noiseValue: value,
      });
    }
  }

  return noiseMap;
};


export function generateWorleyNoise(x, y, scale, numPoints = 100) {
  const points = [];
  for (let i = 0; i < numPoints; i++) {
      points.push({
          x: Math.random() * scale,
          y: Math.random() * scale,
      });
  }

  let minDist = Infinity;
  for (let point of points) {
      const dx = x - point.x;
      const dy = y - point.y;
      const dist = Math.sqrt(dx * dx + dy * dy);
      if (dist < minDist) {
          minDist = dist;
      }
  }

  // Normalize and shift the range from -1 to 1
  return (minDist / scale) * 2 - 1;
}

// Function to generate chunk-based noise with gradient and chunkdistance
export function generateChunkNoise(x, y, chunkDirection, chunkSize) {
  // Calculate chunk coordinates as gradient values +Math.random()*0.1  +(Math.random()-0.5)*0
  const chunkX = x / chunkSize;
  const chunkY = y / chunkSize;
  

  if (chunkDirection === 'x') {
      return chunkX; // Gradient based on X coordinate
  } else if (chunkDirection === 'y') {
      return chunkY; // Gradient based on Y coordinate
  } else if (chunkDirection === 'chunkdistance') {
      // Absolute sum of X and Y chunk positions for chunkdistance
      return Math.abs(chunkX) + Math.abs(chunkY)+(Math.random()-0.5)*0.15; 
  }

  return 0; // Default case
}

export default generateNoiseMap;