// Generate achievements for each terrain detail
const generateTerrainAchievements = () => {
    const achievements = {};

    Object.keys(TERRAIN_DETAILS).forEach(detailKey => {
        const detail = TERRAIN_DETAILS[detailKey];
        const achievementName = detail.name;

        // Achievement for finding and enlarging 1 item (listed first)
        achievements[`enlarge1_${detailKey}`] = {
            name: `Grow 5 ${achievementName}`,
            unlocked: false,
            progress: 0,
            progressGoal: 5,
            description: `Find and grow 5 ${achievementName}.`,
            reward: 50 // Adjust reward as needed
        };


    });
    Object.keys(TERRAIN_DETAILS).forEach(detailKey => {
        const detail = TERRAIN_DETAILS[detailKey];
        const achievementName = detail.name;

        // Achievement for finding and enlarging 20 items (listed after)
        achievements[`enlarge20_${detailKey}`] = {
            name: `Grow 50 ${achievementName}`,
            unlocked: false,
            progress: 0,
            progressGoal: 50,
            description: `Find and grow 50 ${achievementName}.`,
            reward: 500 // Adjust reward as needed
        };


    });



    return achievements;
};