// Interaction.js
// Manages user interactions like dragging and highlighting tiles.

import { config } from './config';
import { generateChunk,sortAllChunks,unloadChunk  } from './Terrain';
import * as PIXI from 'pixi.js';
import { parseColorString, createRadialGradientTexture, lerp } from './utils';
import { removeTerrainDetail, enlargeTerrainDetail } from './TerrainDetails';

export function setupInteraction(app, worldContainer, player, caches) {
    worldContainer.sortableChildren = true;

    const highlight = createHighlight();
    worldContainer.addChild(highlight);

    setupScrolling(app, worldContainer, player, caches, highlight);
    setupZoom(app, worldContainer);
    updateHighlightPosition(worldContainer, app, highlight, caches, player);
}

let bounceVelocity = { x: 0, y: 0 };
let isBouncing = false;
const BOUNCE_STRENGTH = 1; // Adjust this to control bounce strength
const BOUNCE_FRICTION = 0.95; // Adjust this to control how quickly the bounce slows down
const BOUNCE_COOLDOWN = 200; // Cooldown period in milliseconds

let lastValidPosition = { x: 0, y: 0 };
let lastBounceTime = 0;


function setupZoom(app, worldContainer) {
    const MIN_ZOOM = 0.5;
    const MAX_ZOOM = 2;
    const ZOOM_SPEED = 0.01;

    let initialDistance = 0;
    let currentScale = 1;

    function calculateDistance(touch1, touch2) {
        const dx = touch1.clientX - touch2.clientX;
        const dy = touch1.clientY - touch2.clientY;
        return Math.sqrt(dx * dx + dy * dy);
    }

    function handlePinchZoom(event) {
        event.preventDefault();
        if (event.touches.length !== 2) return;

        const touch1 = event.touches[0];
        const touch2 = event.touches[1];

        if (initialDistance === 0) {
            initialDistance = calculateDistance(touch1, touch2);
            return;
        }

        const currentDistance = calculateDistance(touch1, touch2);
        const scaleDiff = currentDistance / initialDistance;

        zoom(scaleDiff);
    }

    function handleTouchEnd() {
        initialDistance = 0;
    }

    function zoom(scaleFactor) {
        const newScale = currentScale * scaleFactor;
        setScale(newScale);
    }

    function setScale(newScale) {
        newScale = Math.max(MIN_ZOOM, Math.min(MAX_ZOOM, newScale));
        if (newScale !== currentScale) {
            const scaleFactor = newScale / currentScale;
            worldContainer.scale.x *= scaleFactor;
            worldContainer.scale.y *= scaleFactor;

            // Adjust position to zoom towards the center
            const centerX = app.screen.width / 2;
            const centerY = app.screen.height / 2;
            worldContainer.position.x = centerX - (centerX - worldContainer.position.x) * scaleFactor;
            worldContainer.position.y = centerY - (centerY - worldContainer.position.y) * scaleFactor;

            currentScale = newScale;
        }
    }

    // Touch events for mobile
    app.view.addEventListener('touchstart', (event) => {
        if (event.touches.length === 2) {
            event.preventDefault();
            initialDistance = calculateDistance(event.touches[0], event.touches[1]);
        }
    });
    app.view.addEventListener('touchmove', handlePinchZoom, { passive: false });
    app.view.addEventListener('touchend', handleTouchEnd);
    app.view.addEventListener('touchcancel', handleTouchEnd);

    // Keyboard events for desktop testing
    window.addEventListener('keydown', (event) => {
        if (event?.key === '+' || event?.key === '=') {
            zoom(1 + ZOOM_SPEED);
        } else if (event?.key === '-' || event?.key === '_') {
            zoom(1 - ZOOM_SPEED);
        }
    });
}

function setupScrolling(app, worldContainer, player, caches, highlight) {
    let isDragging = false;
    let lastPosition = { x: 0, y: 0 };
    let glideVector = { x: 0, y: 0 };
    const MIN_GLIDE_THRESHOLD = 0.1;
    const MAX_GLIDE_SPEED = 5;

    function onDragStart(event) {
        if (Date.now() - lastBounceTime < BOUNCE_COOLDOWN) return;
        isDragging = true;
        lastPosition = event.data.global.clone();
    }

    function onDragMove(event) {
        if (isDragging && Date.now() - lastBounceTime >= BOUNCE_COOLDOWN) {
            const newPosition = event.data.global;
            let dx = newPosition.x - lastPosition.x;
            let dy = newPosition.y - lastPosition.y;

            // Calculate tile position and terrain type
            const screenCenterX = app.screen.width / 2;
            const screenCenterY = app.screen.height / 2;
            const worldCenterX = (screenCenterX - worldContainer.position.x) / worldContainer.scale.x;
            const worldCenterY = (screenCenterY - worldContainer.position.y) / worldContainer.scale.y;

            const tileX = Math.floor(worldCenterX / config.TILE_SIZE);
            const tileY = Math.floor(worldCenterY / config.TILE_SIZE);

            const terrainType = caches.terrainTypeCache.get(`${tileX},${tileY}`);

            // Get both speed adjustment and glide adjustment
            const speedAdjustment = player.getTerrainSpeedAdjustment(terrainType);
            const glideAdjustment = player.getTerrainGlideAdjustment(terrainType);

            // Adjust dx and dy based on speed adjustment
            dx *= speedAdjustment;
            dy *= speedAdjustment;

            // If the terrain allows glide (for example, ice), update glide vector
            if (glideAdjustment > 0) {
                // Reduce glide while dragging
                glideVector.x += dx * 0.3 * glideAdjustment;
                glideVector.y += dy * 0.3 * glideAdjustment;
            } else {
                // If not on glideable terrain, reset glide vector
                glideVector.x = 0;
                glideVector.y = 0;
            }

            // Check if movement is allowed
            if (updateTerrainMovement(worldContainer, app, caches.terrainTypeCache, dx, dy, player)) {
                moveWorldContainer(worldContainer, dx, dy);
                player.updateTrail(dx, dy);
            }

            lastPosition = newPosition.clone();

            checkChunkGeneration(caches, app, worldContainer);
            updateHighlightPosition(worldContainer, app, highlight, caches, player);
        }
    }

    function onDragEnd() {
        isDragging = false;
        updateHighlightPosition(worldContainer, app, highlight, caches, player);
    }

    app.ticker.add(() => {
        const screenCenterX = app.screen.width / 2;
        const screenCenterY = app.screen.height / 2;
        const worldCenterX = (screenCenterX - worldContainer.position.x) / worldContainer.scale.x;
        const worldCenterY = (screenCenterY - worldContainer.position.y) / worldContainer.scale.y;
        const tileX = Math.floor(worldCenterX / config.TILE_SIZE);
        const tileY = Math.floor(worldCenterY / config.TILE_SIZE);

        const terrainType = caches.terrainTypeCache.get(`${tileX},${tileY}`);

        // Get the glide adjustment from the player based on the current terrain
        const glideAdjustment = player.getTerrainGlideAdjustment(terrainType);

        // Apply glide when not dragging
        if (!isDragging && glideAdjustment > 0) {
            glideVector.x *= glideAdjustment;
            glideVector.y *= glideAdjustment;

            // Cap the maximum glide speed
            glideVector.x = Math.min(MAX_GLIDE_SPEED, Math.max(-MAX_GLIDE_SPEED, glideVector.x));
            glideVector.y = Math.min(MAX_GLIDE_SPEED, Math.max(-MAX_GLIDE_SPEED, glideVector.y));
        } else {
            // Stop gliding if not on a glideable terrain or if dragging
            glideVector.x = 0;
            glideVector.y = 0;
        }

        // Apply the glide vector to the world container only if the glide vector is still significant
        if (Math.abs(glideVector.x) > MIN_GLIDE_THRESHOLD || Math.abs(glideVector.y) > MIN_GLIDE_THRESHOLD) {
            moveWorldContainer(worldContainer, glideVector.x, glideVector.y);
            player.updateTrail(glideVector.x, glideVector.y);
        }

        applyBounce(worldContainer);
    });

    app.stage.eventMode = 'static';
    app.stage.hitArea = app.screen;
    app.stage
        .on('pointerdown', onDragStart)
        .on('pointermove', onDragMove)
        .on('pointerup', onDragEnd)
        .on('pointerupoutside', onDragEnd);
}

function moveWorldContainer(worldContainer, dx, dy) {
    worldContainer.position.x += dx;
    worldContainer.position.y += dy;
}

function updateHighlightPosition(worldContainer, app, highlight, caches,player) {
    const screenCenterX = app.screen.width / 2;
    const screenCenterY = app.screen.height / 2;

    const worldCenterX = (screenCenterX - worldContainer.position.x) / worldContainer.scale.x;
    const worldCenterY = (screenCenterY - worldContainer.position.y) / worldContainer.scale.y;

    const tileX = Math.floor(worldCenterX / config.TILE_SIZE);
    const tileY = Math.floor(worldCenterY / config.TILE_SIZE);

    const targetX = tileX * config.TILE_SIZE + config.TILE_SIZE / 2;
    const targetY = tileY * config.TILE_SIZE + config.TILE_SIZE / 2;

    const speed = config.HIGHLIGHT_SPEED;
    highlight.position.x = lerp(highlight.position.x, targetX, speed);
    highlight.position.y = lerp(highlight.position.y, targetY, speed);

    highlight.visible = true;
    
    //triggerTerrainDetailInteraction(tileX, tileY, caches);
    player.handleTerrainInteraction(tileX, tileY, caches);
    //removeTerrainDetail(tileX,tileY,caches);
}



function updateTerrainMovement(worldContainer, app, terrainTypeCache, dx, dy,player) {
    const screenCenterX = app.screen.width / 2;
    const screenCenterY = app.screen.height / 2;

    const worldCenterX = (screenCenterX - worldContainer.position.x) / worldContainer.scale.x;
    const worldCenterY = (screenCenterY - worldContainer.position.y) / worldContainer.scale.y;

    const tileX = Math.floor(worldCenterX / config.TILE_SIZE);
    const tileY = Math.floor(worldCenterY / config.TILE_SIZE);

    const terrainType = terrainTypeCache.get(`${tileX},${tileY}`);

    if (player.getBlockedTerrain()?.includes(terrainType)) {

        isBouncing = true;
        bounceVelocity.x = -dx * BOUNCE_STRENGTH;
        bounceVelocity.y = -dy * BOUNCE_STRENGTH;
        
        // Restore last valid position
        worldContainer.position.x = lastValidPosition.x;
        worldContainer.position.y = lastValidPosition.y;
        
        lastBounceTime = Date.now();
        return false;
    }

    // If movement is allowed, update the last valid position
    lastValidPosition.x = worldContainer.position.x;
    lastValidPosition.y = worldContainer.position.y;

    return true;
}

function applyBounce(worldContainer) {
    if (!isBouncing) return;

    const newX = lerp(worldContainer.position.x, worldContainer.position.x + bounceVelocity.x, 0.1);
    const newY = lerp(worldContainer.position.y, worldContainer.position.y + bounceVelocity.y, 0.1);

    moveWorldContainer(worldContainer, newX - worldContainer.position.x, newY - worldContainer.position.y);

    bounceVelocity.x *= BOUNCE_FRICTION;
    bounceVelocity.y *= BOUNCE_FRICTION;

    if (Math.abs(bounceVelocity.x) < 0.1 && Math.abs(bounceVelocity.y) < 0.1) {
        isBouncing = false;
        bounceVelocity.x = 0;
        bounceVelocity.y = 0;
    }
}


function createHighlight() {
    const highlight = new PIXI.Container();

    const highlightColor = parseColorString(config.HIGHLIGHT_COLOR);

    const highlightTexture = createRadialGradientTexture(config.TILE_SIZE / 1.5, highlightColor, 0.2);
    const highlightSprite = new PIXI.Sprite(highlightTexture);
    highlightSprite.anchor.set(0.5, 0.5);

    highlight.addChild(highlightSprite);

    highlight.zIndex = 1000;

    highlight.visible = false;

    return highlight;
}

export function checkChunkGeneration(caches, app, worldContainer) {
    const { tileCache, textureNoiseMaps, chunks, simplex, detailCache, terrainTypeCache } = caches;

    const cameraX = -worldContainer.position.x / (config.TILE_SIZE * worldContainer.scale.x);
    const cameraY = -worldContainer.position.y / (config.TILE_SIZE * worldContainer.scale.y);

    // Calculate chunk range to load, factoring in the scale of the world container
    const minChunkX = Math.floor((cameraX - config.LOAD_DISTANCE) / config.CHUNK_SIZE);
    const maxChunkX = Math.ceil((cameraX + config.VISIBLE_TILES + config.LOAD_DISTANCE) / config.CHUNK_SIZE);
    const minChunkY = Math.floor((cameraY - config.LOAD_DISTANCE) / config.CHUNK_SIZE);
    const maxChunkY = Math.ceil((cameraY + config.VISIBLE_TILES + config.LOAD_DISTANCE) / config.CHUNK_SIZE);

    let newChunksGenerated = false;

    // Load new chunks within the visible range
    for (let x = minChunkX; x <= maxChunkX; x++) {
        for (let y = minChunkY; y <= maxChunkY; y++) {
            const chunkKey = `${x},${y}`;
            if (!chunks[chunkKey]) {
                generateChunk(
                    x,
                    y,
                    tileCache,
                    textureNoiseMaps,
                    app,
                    chunks,
                    simplex,
                    worldContainer,
                    detailCache,
                    terrainTypeCache
                );
                newChunksGenerated = true;
            }
        }
    }

    // Unload chunks outside the visible range
    Object.keys(chunks).forEach((chunkKey) => {
        const [chunkX, chunkY] = chunkKey.split(',').map(Number);
        if (
            chunkX < minChunkX - config.UNLOAD_DISTANCE ||
            chunkX > maxChunkX + config.UNLOAD_DISTANCE ||
            chunkY < minChunkY - config.UNLOAD_DISTANCE ||
            chunkY > maxChunkY + config.UNLOAD_DISTANCE
        ) {
            // Unload chunk if it's outside the extended unload distance
            unloadChunk(chunkX, chunkY, chunks, tileCache, detailCache, worldContainer);
        }
    });

    if (newChunksGenerated) {
        // Sort all chunks after generating new ones
        sortAllChunks(chunks, worldContainer);
    }
}