import React, { useEffect, useRef, useState } from 'react';
import * as PIXI from 'pixi.js';
import { createPlayer } from './Player';
import { setupWorld } from './World';
import { setupInteraction } from './Interaction';
import { config } from './config';
import PlayerMenu from './PlayerMenu'; // Import the new PlayerMenu component
import { DEFAULT_PLAYER_STATE } from './playerState';
import { ToastContainer } from 'react-toastify'; // Import ToastContainer
import 'react-toastify/dist/ReactToastify.css'; // Import Toastify CSS
import './CustomToast.css'; // custom styles
import './App.css';
import { manageDrawings } from './Animals'; 

export function OldApp() {
  const appRef = useRef(null);
  const playerStateRef = useRef(DEFAULT_PLAYER_STATE);
  const [playerState, setPlayerState] = useState(playerStateRef.current);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const playerRef = useRef(null);


  useEffect(() => {
    const app = new PIXI.Application({
      width: window.innerWidth,
      height: window.innerHeight,
      backgroundColor: config.BACKGROUND_COLOR,
      resizeTo: window,
    });

    appRef.current.appendChild(app.view);

    const worldContainer = new PIXI.Container();
    app.stage.addChild(worldContainer);

    const player = createPlayer(app, playerStateRef, setPlayerState, setIsMenuOpen);
    playerRef.current = player;

    const caches = setupWorld(app, worldContainer, player);
    setupInteraction(app, worldContainer, player, caches);

    // Call the function to draw the pastel blue circle after everything is set up
    //manageDrawings(app);

    return () => {
      app.destroy(true, true);
    };
  }, []);

  return (
    <div>
      <PlayerMenu
        playerState={playerState}
        playerRef={playerRef}
        isMenuOpen={isMenuOpen}
        setIsMenuOpen={setIsMenuOpen}
      />

      <div ref={appRef} id="game-container" />

      <ToastContainer
        autoClose={2500} // short duration
        hideProgressBar={true} // no progress bar
        closeOnClick={true} // no close button
        pauseOnHover={false} // no pause on hover
        className="custom-toast-container" // custom container class
        toastClassName="custom-toast" // base custom toast styling
        position="top-left" // set position to top-right
        limit={5} // limit the number of toasts
        newestOnTop={true} // stack the newest toast on top
        gap={0} // set gap between toasts
      />
    </div>
  );
}

export default OldApp;


