const DEFAULT_DETAIL = 'none';

// Default movement animation: Slight left-right movement
export function defaultMoveAnimation(sprite, app) {
  const moveDistance = 3; // Move 5 pixels left and right
  const speed = 0.1; // Speed of movement
  let direction = Math.random() < 0.5 ? -1 : 1; // 1 = moving right
  app?.ticker.add(() => {
    sprite.x += speed * direction;
    
    // Change direction when reaching the moveDistance
    if (sprite.x > sprite.startX + moveDistance || sprite.x < sprite.startX - moveDistance) {
      direction *= -1;
    }
  });
}
export function beeMoveAnimation(sprite, app) {
  const maxMoveDistance = 10; // Max distance the bee can fly from origin
  const speed = 1.2; // Speed of movement
  const verticalSpeed = 0.8; // Speed for vertical up and down motion
  let directionX = Math.random() < 0.5 ? -1 : 1; // 1 = moving right
  let directionY = Math.random() < 0.5 ? -1 : 1; // 1 = moving down
  const turnProbability = 0.01; // Probability of changing direction at any point
  const returnProbabilityScale = 0.05; // The further away, the more likely it is to turn back

  app?.ticker.add(() => {
    // Move the sprite horizontally
    sprite.x += speed * directionX;

    // Move the sprite vertically with random up/down motion
    sprite.y += verticalSpeed * directionY;

    // Flip the sprite when flying left or right
    sprite.scale.x = directionX > 0 ? -Math.abs(sprite.scale.x) : Math.abs(sprite.scale.x);

    // Calculate distance from origin
    const distanceFromOrigin = Math.sqrt(
      Math.pow(sprite.x - sprite.startX, 2) + Math.pow(sprite.y - sprite.startY, 2)
    );

    // Randomly decide to change horizontal or vertical direction
    if (Math.random() < turnProbability) {
      directionX *= -1;
    }
    if (Math.random() < turnProbability) {
      directionY *= -1;
    }

    // Increase the chance of returning towards origin based on distance
    if (distanceFromOrigin > maxMoveDistance || Math.random() < distanceFromOrigin * returnProbabilityScale) {
      directionX = sprite.x > sprite.startX ? -1 : 1; // Turn back towards the origin horizontally
    }

    // Ensure the bee doesn't fly too far vertically
    if (sprite.y > sprite.startY + maxMoveDistance || sprite.y < sprite.startY - maxMoveDistance) {
      directionY *= -1;
    }
  });
}

