export function generateTerrainDetails(detailCache) {
  for (const [name, detail] of Object.entries(TERRAIN_DETAILS)) {
    let texture;
    if (detail.type === 'svg') {
      const blob = new Blob([detail.content], { type: 'image/svg+xml' });
      const url = URL.createObjectURL(blob);
      texture = PIXI.Texture.from(url);
    } else if (detail.type === 'image') {
      texture = PIXI.Texture.from(detail.content);
      texture.baseTexture.on('error', (error) => {
        console.error('Error loading texture:', detail.content, error);
      });
    }
    detailCache.set(name, texture);
  }
}


export function createTerrainDetail(terrain, detailCache, x, y, detailContainer, worldX, worldY,app) {
  const terrainMapping = TERRAIN_DETAIL_MAPPING.find((t) => t.terrain === terrain) || {
    details: [DEFAULT_DETAIL],
    weights: [1],
  };

  const detailChance = terrainMapping.detailChance !== undefined 
    ? terrainMapping.detailChance * config.DETAIL_CHANCE 
    : config.DETAIL_CHANCE * 0.1;

  if (Math.random() >= detailChance || terrainMapping.details.length === 0) {
    return null;
  }

  const randomValue = Math.random();
  let cumulativeWeight = 0;
  let selectedDetail = DEFAULT_DETAIL;

  for (let i = 0; i < terrainMapping.details.length; i++) {
    cumulativeWeight += terrainMapping.weights[i];
    if (randomValue < cumulativeWeight) {
      selectedDetail = terrainMapping.details[i];
    //  console.log("Selected detail:", selectedDetail);
      break;
    }
  }

  // Now call the function that handles the actual placement
  const sprite = placeTerrainDetail(selectedDetail, detailCache, x, y, detailContainer, worldX, worldY);

   // Check if the terrain detail is marked as moving and apply the movement
   const detailInfo = TERRAIN_DETAILS[selectedDetail];
   if (detailInfo?.moving ) {

     sprite.startX = sprite.x; // Save the original X position
     const moveFunc = detailInfo?.moveFunction || defaultMoveAnimation;
     moveFunc(sprite, app); // Call the move function
   }
 
   return sprite;
}

export function placeTerrainDetail(selectedDetail, detailCache, x, y, detailContainer, worldX, worldY, angle = null) {
  const texture = detailCache.get(selectedDetail);
  let finalSprite;

  // If angle is provided, use the split-and-merge function
  if (angle !== null && selectedDetail === 'arrow') {
    finalSprite = splitAndMergeTexture(texture, 130, angle); // Split at 120px and rotate top
  } else {
    // Default behavior: Just use the texture as is
    finalSprite = new PIXI.Sprite(texture);
  }

  // Add random variation to scale based on config.DETAIL_SCALE_VARIATION
  const baseScale = TERRAIN_DETAILS[selectedDetail]?.scale || DEFAULT_TERRAIN_SCALE;
  const finalScale = getFinalScale(baseScale, config.DETAIL_SCALE_VARIATION, config.DETAIL_SCALE_SKEWPOWER, config.DETAIL_SCALE_EXTREMECHANCE, 0.1);
  finalSprite.scale.set(finalScale);

  // Add random variation to position based on config.DETAIL_OFFSET_VARIATION
  const baseOffsetX = TERRAIN_DETAILS[selectedDetail]?.offsetX || 0;
  const baseOffsetY = TERRAIN_DETAILS[selectedDetail]?.offsetY || 0;
  const offsetXVariation = (Math.random() - 0.5) * 2 * config.DETAIL_OFFSET_VARIATION;
  const offsetYVariation = (Math.random() - 0.5) * 2 * config.DETAIL_OFFSET_VARIATION;
  const finalOffsetX = baseOffsetX + offsetXVariation;
  const finalOffsetY = baseOffsetY + offsetYVariation;

  finalSprite.position.set(x * config.TILE_SIZE + finalOffsetX, y * config.TILE_SIZE + finalOffsetY);
  detailContainer.addChild(finalSprite);

  // Track the terrain detail in the cache by tile coordinates
  const key = `${worldX},${worldY}`;
  if (!detailCache.has(key)) {
    detailCache.set(key, []);
  }

  // Store both the sprite and the key (selectedDetail)
  detailCache.get(key).push({ sprite: finalSprite, detailKey: selectedDetail });

  return finalSprite;
}

function splitAndMergeTexture(texture, splitY, angle) {
  const baseTexture = texture.baseTexture;

  // Split the texture into two parts: top and bottom
  const topTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(0, 0, texture.width, splitY));
  const bottomTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(0, splitY, texture.width, texture.height - splitY));

  // Create an offscreen canvas to draw both parts
  const canvas = document.createElement('canvas');
  canvas.width = texture.width;
  canvas.height = texture.height;
  const context = canvas.getContext('2d');

  // Draw the bottom part as is (without rotation)
  context.drawImage(
    bottomTexture.baseTexture.resource.source,   // Source image
    0,                                          // Source x
    splitY,                                     // Source y (starting from the split line)
    texture.width,                              // Source width
    texture.height - splitY,                    // Source height (from splitY to the bottom)
    0,                                          // Destination x
    splitY,                                     // Destination y (draw at the split line)
    texture.width,                              // Destination width
    texture.height - splitY                     // Destination height
  );

  // If an angle is provided, rotate and draw the top part
  if (angle !== null) {
    context.save();
    context.translate(texture.width / 2, splitY); // Set the pivot point at the bottom of the top part
    context.rotate(angle);                         // Rotate the canvas

    // Draw the rotated top part
    context.drawImage(
      topTexture.baseTexture.resource.source,  // Source image
      0,                                       // Source x
      0,                                       // Source y (from the top of the image)
      texture.width,                           // Source width
      splitY,                                  // Source height (from 0 to the split line)
      -texture.width / 2,                      // Destination x (center the image after rotation)
      -splitY,                                 // Destination y (draw the top part at the correct position)
      texture.width,                           // Destination width
      splitY                                   // Destination height
    );

    context.restore();
  } else {
    // No rotation, draw the top part as is
    context.drawImage(
      topTexture.baseTexture.resource.source,  // Source image
      0,                                       // Source x
      0,                                       // Source y (from the top of the image)
      texture.width,                           // Source width
      splitY,                                  // Source height (from 0 to the split line)
      0,                                       // Destination x
      0,                                       // Destination y (draw at the top)
      texture.width,                           // Destination width
      splitY                                   // Destination height
    );
  }

  // Create a new PIXI texture from the canvas
  const newBaseTexture = PIXI.BaseTexture.from(canvas);
  const newTexture = new PIXI.Texture(newBaseTexture);

  // Create a new sprite using the merged texture
  const mergedSprite = new PIXI.Sprite(newTexture);
  return mergedSprite;
}





// Generate a random number within [min, max] skewed toward the center
function generateSkewedRandom(min, max, skewPower = 4) {
  const randomValue = Math.pow(Math.random(), skewPower); // Bias towards the center
  return mapValueToRange(randomValue, 0, 1, min, max); // Map to [min, max]
}

// Generate a random number near the extremes (either close to min or max)
function generateExtremeRandom(min, max, extremeRangeRatio = 0.1) {
  const isMinSide = Math.random() < 0.5; // Decide if it should be near min or max
  const extremeOffset = (max - min) * extremeRangeRatio; // 10% near min or max
  return isMinSide ? min + Math.random() * extremeOffset : max - Math.random() * extremeOffset;
}

// Map a value in range [a, b] to [c, d]
function mapValueToRange(value, a, b, c, d) {
  return c + (d - c) * (value - a) / (b - a);
}

// Main function to select between skewed and extreme values based on probability
function getFinalScale(base, variationRange, skewPower, extremeProbability, extremeRangeRatio = 0.1) {
  const minScale = base - variationRange;
  const maxScale = base + variationRange;

  // Randomly decide if it should be an extreme value or a skewed middle value
  return Math.random() < extremeProbability
    ? generateExtremeRandom(minScale, maxScale, extremeRangeRatio) // Pick extreme value
    : generateSkewedRandom(minScale, maxScale, skewPower); // Pick skewed center value
}

export function removeTerrainDetail(tileX, tileY, caches) {
  const { detailCache, chunks } = caches;
  const key = `${tileX},${tileY}`;
  console.log("Removing terrain detail at", key);

  const chunkContainer = getChunkContainerForTile(tileX, tileY, chunks);
  let removedDetails = [];

  if (chunkContainer && detailCache.has(key)) {
    const details = detailCache.get(key);
    details.forEach(({ sprite, detailKey }) => {
      const detail = TERRAIN_DETAILS[detailKey];
      if (!detail) {
        console.warn(`Detail key "${detailKey}" is invalid or missing from TERRAIN_DETAILS.`);
        return; // Skip this iteration if detail is not found
      }

      if (sprite) {
        chunkContainer.removeChild(sprite);
        sprite.destroy();
        removedDetails.push({
          detailKey: detailKey,
          scale: sprite.scale.x,
          size: { width: sprite.width, height: sprite.height },
        });
      }
    });
    detailCache.delete(key);
    return removedDetails;
  }
  return null;
}

export function enlargeTerrainDetail(tileX, tileY, caches) {
  const { detailCache, chunks } = caches;
  const ENLARGEMENT_FACTOR = config.TERRAIN_DETAIL_ENLARGEMENT_FACTOR;
  const key = `${tileX},${tileY}`;

  const chunkContainer = getChunkContainerForTile(tileX, tileY, chunks);
  let didEnlarge = false;
  let enlargedDetails = [];
 // console.log("Enlarging terrain detail at", key);
  if (chunkContainer && detailCache.has(key)) {
    const details = detailCache.get(key);
    details.forEach(({ sprite, detailKey }) => {
      // Ensure that the detailKey exists in TERRAIN_DETAILS
      const detail = TERRAIN_DETAILS[detailKey];
      if (!detail) {
        console.warn(`Detail key "${detailKey}" is invalid or missing from TERRAIN_DETAILS.`);
        return; // Skip this iteration if detail is not found
      }

      if (sprite && !sprite.isEnlarged) {
        const currentScale = sprite.scale.x; // Assuming uniform scale
        const newScale = currentScale * ENLARGEMENT_FACTOR;

        const originalPosition = { x: sprite.x, y: sprite.y };
        sprite.scale.set(newScale);

        const textureWidth = sprite.texture.width;
        const textureHeight = sprite.texture.height;
        const positionShiftX = (textureWidth * (newScale - currentScale)) / ENLARGEMENT_FACTOR;
        const positionShiftY = (textureHeight * (newScale * 1.1 - currentScale)) / ENLARGEMENT_FACTOR;

        sprite.position.set(originalPosition.x - positionShiftX, originalPosition.y - positionShiftY);
        sprite.isEnlarged = true;
        didEnlarge = true;

        enlargedDetails.push({
          detailKey: detailKey,
          scale: newScale,
          size: { width: sprite.width, height: sprite.height },
          originalScale: currentScale,
        });
      }
    });
  }

  return didEnlarge ? enlargedDetails : null;
}

export function getChunkContainerForTile(tileX, tileY, chunks) {
  // Calculate which chunk the tile belongs to
  const chunkX = Math.floor(tileX / config.CHUNK_SIZE);
  const chunkY = Math.floor(tileY / config.CHUNK_SIZE);

  // Construct the chunk key
  const chunkKey = `${chunkX},${chunkY}`;

  // Log to see if the correct chunk is being accessed
  //console.log("Fetching chunk container for:", chunkKey);

  // Return the chunk container if it exists, otherwise return null
  return chunks[chunkKey] || null;
}
