import * as PIXI from 'pixi.js';
import { parseColorString, createRadialGradientTexture } from './utils';
import { config } from './config';
import { updatePlayerColor, createPlayerState } from './playerState';

// Add new config values at the top of the file


export function createPlayer(app, playerStateRef, setPlayerState, setIsMenuOpen) {
    const playerContainer = new PIXI.Container();
    playerContainer.interactive = true;
    playerContainer.buttonMode = true;

    const baseColor1 = 0xb945af;// parseColorString(config.PLAYER_BASE_COLOR);
    const baseColor2 = parseColorString(config.PLAYER_BASE_COLOR2); // Secondary color for the layers
    const layerCount = config.PLAYER_LAYER_COUNT;
    const maxSize = config.PLAYER_MAX_SIZE;

    // Create the glow layers from the old code
    const layers = createGlowLayer(playerContainer, baseColor1, layerCount, maxSize);

    // Create core
    const core = new PIXI.Graphics();
    core.beginFill(0xfafce1);
    core.drawCircle(0, 0, config.PLAYER_CORE_SIZE / 2);
    core.endFill();

    // Create trail
    const trail = new PIXI.Container();

    playerContainer.addChild(trail, core);

    playerContainer.x = Math.floor(window.innerWidth / 2);
    playerContainer.y = Math.floor(window.innerHeight / 2);

    const player = {
        container: playerContainer,
        core,
        layers, // Glow layers
        trail,
        trailParticles: [],
        updateTrail: (dx, dy) => updateTrail(player, dx, dy),
    };

    const playerStateManager = createPlayerState(playerStateRef, setPlayerState, player, setIsMenuOpen);

    Object.assign(player, playerStateManager);
    updatePlayerColor(player, playerStateRef.current.color);

    let time = 0;
    const maxOffset = config.TILE_SIZE / 2;
    animatePlayer(player, config, app, maxOffset, layerCount, time);

    app.stage.addChild(playerContainer);

    return player;
}

function animatePlayer(player, config, app, maxOffset, layerCount, time) {
    app.ticker.add((delta) => {
        time += config.PLAYER_PULSE_SPEED * delta;
        const basePulse = Math.sin(time * 1.2) * 0.1 + 1;

        player.layers.forEach((layer, index) => {
            const layerPulse = basePulse + Math.sin(time * 0.3) * 0.1;
            layer.sprite.scale.set(layerPulse, layerPulse);

            // Update color based on pulse
            const t = index / (layerCount - 1);
            const baseLayerColor =  parseColorString(config.PLAYER_BASE_COLOR)// player.baseColor; //interpolateColor(player.baseColor, parseColorString(config.PLAYER_BASE_COLOR2), t);
            const brightnessFactor = 1 + (layerPulse - 1) * 0.8; // Adjust this factor to control color intensity
            const pulsedColor = adjustBrightness(baseLayerColor, brightnessFactor);
            layer.sprite.tint = pulsedColor;

            const springForceX = -config.SPRING_STRENGTH * layer.offsetX;
            const springForceY = -config.SPRING_STRENGTH * layer.offsetY;

            layer.velocity.x += springForceX;
            layer.velocity.y += springForceY;

            layer.velocity.x *= config.DAMPING;
            layer.velocity.y *= config.DAMPING;

            layer.offsetX += layer.velocity.x * delta;
            layer.offsetY += layer.velocity.y * delta;

            const offsetLength = Math.sqrt(layer.offsetX ** 2 + layer.offsetY ** 2);
            if (offsetLength > maxOffset) {
                const scale = maxOffset / offsetLength;
                layer.offsetX *= scale;
                layer.offsetY *= scale;
            }

            const reversedIndex = layerCount - index;
            layer.sprite.x = layer.offsetX * (reversedIndex / layerCount);
            layer.sprite.y = layer.offsetY * (reversedIndex / layerCount);
        });

        const bounce = Math.sin(time * config.PLAYER_BOUNCE_FACTOR) * (config.TILE_SIZE / 10);
        player.container.y = Math.floor(window.innerHeight / 2) + bounce;

        updateTrailParticles(player);
    });
}
// Track the time since the last particle spawn
let lastParticleSpawnTime = 0;

function updateTrail(player, dx, dy, deltaTime) {
    // Control how often particles are spawned (adjust the spawn interval as needed)
    const spawnInterval = 50; // in milliseconds or adjust to your preference

    // Only spawn a particle if enough time has passed
    if (performance.now() - lastParticleSpawnTime > spawnInterval) {
        const particle = new PIXI.Graphics();
        particle.beginFill(parseColorString(config.PLAYER_BASE_COLOR));
        particle.drawCircle(0, 0, config.PLAYER_CORE_SIZE / 4);
        particle.endFill();

        // Offset the starting position of the particle to half the player's width in the direction of movement
        const playerHalfWidth = config.PLAYER_CORE_SIZE / 2;
        const directionMagnitude = Math.sqrt(dx * dx + dy * dy);
        const offsetX = (dx / directionMagnitude) * playerHalfWidth;
        const offsetY = (dy / directionMagnitude) * playerHalfWidth;

        particle.x = offsetX;
        particle.y = offsetY;

        particle.vx = dx * config.PLAYER_TRAIL_SPEED + Math.sign(dx) * config.PLAYER_TRAIL_SPEED_MINIMUM;
        particle.vy = dy * config.PLAYER_TRAIL_SPEED + Math.sign(dy) * config.PLAYER_TRAIL_SPEED_MINIMUM;

        particle.alpha = 0.5;
        particle.lifetime = config.PLAYER_TRAIL_PARTICLE_LIFETIME;

        player.trail.addChild(particle);
        player.trailParticles.push(particle);

        // Limit particle count
        if (player.trailParticles.length > config.PLAYER_TRAIL_PARTICLE_COUNT) {
            const oldestParticle = player.trailParticles.shift();
            player.trail.removeChild(oldestParticle);
        }

        // Update last spawn time
        lastParticleSpawnTime = performance.now();
    }
}

function updateTrailParticles(player) {
    for (let i = player.trailParticles.length - 1; i >= 0; i--) {
        const particle = player.trailParticles[i];
        particle.x += particle.vx;
        particle.y += particle.vy;
        particle.alpha *= 0.95;
        particle.scale.x *= 0.95;
        particle.scale.y *= 0.95;
        particle.lifetime--;

        if (particle.lifetime <= 0) {
            player.trail.removeChild(particle);
            player.trailParticles.splice(i, 1);
        }
    }
}


function createGlowLayer(playerContainer, baseColor1, layerCount =1, maxSize) {
    const layers = [];
    for (let i = 0; i < layerCount; i++) {
        const size = maxSize/1.8;
        const alpha = config.PLAYER_COLOR_PULSE_INTENSITY;
        const t = i / (layerCount - 1);
        const layer = createLayer(size, alpha, baseColor1);
        playerContainer.addChild(layer);
        layers.push({
            sprite: layer,
            offsetX: 0,
            offsetY: 0,
            velocity: { x: 0, y: 0 },
        });
    }
    return layers;
}

function createLayer(size, alpha, color) {
    const layer = new PIXI.Graphics();
    const texture = createRadialGradientTexture(size, color, alpha);
    const sprite = new PIXI.Sprite(texture);
    sprite.anchor.set(0.5, 0.5);
    sprite.width = sprite.height = size * 2;

    return sprite;
}

function adjustBrightness(color, factor) {
    const r = Math.min(255, Math.round(((color >> 16) & 255) * factor));
    const g = Math.min(255, Math.round(((color >> 8) & 255) * factor));
    const b = Math.min(255, Math.round((color & 255) * factor));
    return (r << 16) | (g << 8) | b;
}

function interpolateColor(color1, color2, t) {
    const r1 = (color1 >> 16) & 255,
        g1 = (color1 >> 8) & 255,
        b1 = color1 & 255;
    const r2 = (color2 >> 16) & 255,
        g2 = (color2 >> 8) & 255,
        b2 = color2 & 255;

    const r = Math.round(r1 + (r2 - r1) * t);
    const g = Math.round(g1 + (g2 - g1) * t);
    const b = Math.round(b1 + (b2 - b1) * t);

    return (r << 16) | (g << 8) | b;
}
